.lcard{
  position: relative;
    padding: 162px 100px 60px;
    background: rgb(250, 250, 250);
    border-radius: 48px;
}
.lcard:before, .lcard:after {
  content: "";
  position: absolute;
  top: 50px;
  border-radius: 34px;
}

.lcard:after {
  left: 17px;
  right: 17px;
  bottom: -16px;
  z-index: -1;
  background: #FFC700;
}

